import {
    auth,
    ref,
    database,
} from './firebase';
import createIframe from './createIframe';

window.sgliveSSO = {
    getPage(id, callback) {
        const websiteRef = ref.child(`websites/${id}`);
        websiteRef
            .once('value')
            .then((snap) => {
                callback(snap.val());
            });
    },
    savePage(id, callback) {
        const websitesRef = ref.child(`websites`);
        websitesRef
            .once('value')
            .then((snap) => {
                if (snap.hasChild(id)) {
                    websitesRef
                        .child(id)
                        .update({
                            updatedAction: 'Edited',
                            updatedBy: auth().currentUser.uid,
                            updatedDate: database.ServerValue.TIMESTAMP,
                        })
                        .then(() => {
                            const websitesLogRef = ref.child(`websitesLog/${id}`);
                            websitesLogRef
                                .push({
                                    updatedAction: 'Edited',
                                    updatedBy: auth().currentUser.uid,
                                    updatedDate: database.ServerValue.TIMESTAMP,
                                })
                                .then(() => {
                                    callback('edited');
                                });
                        });
                } else {
                    callback('not exists');
                }
            });
    },
    publishPage(id, callback) {
        const websitesRef = ref.child(`websites`);
        websitesRef
            .once('value')
            .then((snap) => {
                if (snap.hasChild(id)) {
                    websitesRef
                        .child(id)
                        .update({
                            updatedAction: 'Published',
                            updatedBy: auth().currentUser.uid,
                            updatedDate: database.ServerValue.TIMESTAMP,
                        })
                        .then(() => {
                            const websitesLogRef = ref.child(`websitesLog/${id}`);
                            websitesLogRef
                                .push({
                                    updatedAction: 'Published',
                                    updatedBy: auth().currentUser.uid,
                                    updatedDate: database.ServerValue.TIMESTAMP,
                                })
                                .then(() => {
                                    callback('published');
                                });
                        });
                } else {
                    callback('not exists');
                }
            });
    },
    initializeClient(callback) {
        const contentWindow = createIframe();
        window.addEventListener('message', (event) => {
            const message = event.data;
            if (message.type === 'sglAccessToken') {
                const {
                    sglAccessToken,
                } = message.data;
                if (sglAccessToken) {
                    auth()
                        .signInWithCustomToken(sglAccessToken)
                        .then((data) => {
                            callback({
                                user: auth().currentUser,
                            });
                        })
                        .catch((error) => {
                            if (error.code === 'auth/invalid-custom-token' && auth().currentUser) {
                                callback({
                                    user: auth().currentUser,
                                });
                            } else {
                                callback({
                                    user: null,
                                });
                            }
                        });
                } else {
                    auth()
                        .signOut()
                        .then((data) => {
                            callback({
                                user: null,
                            });
                        })
                        .catch((error) => {
                            callback({
                                user: null,
                            });
                        });
                }
            }
        });
    },
};



